import { Editor } from 'slate-react'
import { Value } from 'slate'

import React from 'react'
import { Button, Icon, Toolbar } from '../components'

const initialValue = Value.fromJSON({
  "object": "value",
  "document": {
    "object": "document",
    "data": {},
    "nodes": [
      {
        "object": "block",
        "type": "paragraph",
        "isVoid": false,
        "data": {},
        "nodes": [
          {
            "object": "text",
            "leaves": [
              {
                "object": "leaf",
                "text": "Brown ",
                "marks": []
              }
            ]
          },
          {
            "object": "inline",
            "type": "inline-input",
            "isVoid": false,
            "data": {
              "text": ""
            },
            "nodes": [
              {
                "object": "text",
                "leaves": [
                  {
                    "object": "leaf",
                    "text": " ",
                    "marks": []
                  }
                ]
              }
            ]
          },
          {
            "object": "text",
            "leaves": [
              {
                "object": "leaf",
                "text": " jumps over the lazy dog",
                "marks": []
              }
            ]
          }
        ]
      }
    ]
  }
})

/**
 * The inline-input example.
 *
 * @type {Component}
 */

class InlineInput extends React.Component {
  /**
   * Deserialize the raw initial value.
   *
   * @type {Object}
   */

  state = {
    value: initialValue,
  }

  /**
   * Render the app.
   *
   * @return {Element} element
   */

  render() {
    return (
      <div>
        <Toolbar>
          <Button onMouseDown={this.onAddInlineInput}>
            [+ Input]
          </Button>
        </Toolbar>
        <Editor
          placeholder="Inline input"
          value={this.state.value}
          onChange={this.onChange}
          renderNode={this.renderNode}
        />
      </div>
    )
  }

  /**
   * Render a Slate node.
   *
   * @param {Object} props
   * @return {Element}
   */

  renderNode = props => {
    const { attributes, children, node, isFocused } = props

    switch (node.type) {
      case 'paragraph': {
        return <p {...attributes}>{children}</p>
      }
      case 'inline-input': {
        return (
          <span
            style={{border: '1px solid black', width: '1.5em', height: '1.5em', lineHeight: '1.5em', display: 'inline-block'}}
            {...props.attributes}
          >
            {props.children}
          </span>
        )
      }
    }
  }

  /**
   * On change.
   *
   * @param {Change} change
   */

  onChange = ({ value }) => {
    this.setState({ value })
  }

  /**
   * When clicking button, insert a square inline input
   *
   * @param {Event} e
   */

  onAddInlineInput = (e) => {
    e.preventDefault()
    const { value } = this.state
    const change = value.change()

    change.insertInline({
      type: 'inline-input',
      isVoid: false,
      nodes: [
        {
          object: "text",
          leaves: [
            {
              object: "leaf",
              text: " ", // 1 space to make the square input appear
              marks: []
            }
          ]
        }
      ]
    }).focus()

    this.onChange(change)
  }
}

/**
 * Export.
 */

export default InlineInput



